@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
    font-family: 'Roboto', sans-serif;
}

* {
    margin:0;
    box-sizing: border-box;
}

.nav {
    height:65px;
    padding:50px;
    background-color:black;
    color:white;
    display:flex;
    justify-content: center;
    align-items: center;
}
.nav h1 {
    font-size:2rem;
}
.nav img {
    height: 75px;
    margin-right:24px;
}
.game {
    height: 78vh;
    padding: 24px 0px 24px 0px;
}
.cardList {
    padding:12px;
    padding-left: 100px;
    padding-right: 100px;
    height: 100%;
    border:2px solid black;
    border-radius: 6px;

    display:grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 24px;
    gap: 24px;
}
.card {
    border: 4px solid black;
    border-radius: 12px;
    display: flex;
    align-items: center;
    transition: 25ms;
}
.card img {
    height:100%;
}
.card p {
    font-size: 1.5rem;
    font-weight: 600;
}
.card:hover {
    background-color: gray;
    color:white;
}
.card:active {
    background-color: black;
}

.scoreBoard {
    height:50px;
    display:flex;
    justify-content: center;
    align-items: center;
}
.scoreBoard p {
    font-weight: 600;
    font-size: 1.5rem;
    margin:24px;
}
.menu {
    height:87.5vh;
    padding:36px;
}
.menu h2 {
    text-align: center;
    font-size: 10rem;
    border-bottom:3px solid black;
}
.menu p {
    margin-top: 24px;
    margin-bottom:36px;
    font-size: 4rem;
    text-align: center;
}
.menu button {
    width: 100%;
    height:40vh;
    border:0;
    background-color: white;
    font-size: 4rem;
    font-weight: 600;
    border:4px solid black;
}
.menu button:hover {
    background-color:gray;
    color:white;
}
.menu button:active {
    background-color:black;
}
.gameOver {
    height:87.5vh;
    padding:36px;
}
.gameOver h2 {
    text-align: center;
    font-size: 10rem;
    border-bottom:3px solid black;
}
.gameOver p {
    margin-top: 24px;
    margin-bottom:36px;
    font-size: 4rem;
    text-align: center;
}
.gameOver button {
    width: 100%;
    height:40vh;
    border:0;
    background-color: white;
    font-size: 4rem;
    font-weight: 600;
    border:4px solid black;
}
.gameOver button:hover {
    background-color:gray;
    color:white;
}
.gameOver button:active {
    background-color:black;
}
